@import url(https://fonts.googleapis.com/css?family=Marck+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.About-us{
  background-color: #b47984;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  margin: 100px auto;
  padding: 50px;
  font-size: 1.8rem;
  font-family: "Marck Script", cursive;
  border-radius: 4px;
  box-shadow:  rgb(56, 56, 56) 2px 2px;
}
.About-us h1, .About-us p{
    margin: 0px;
    color: #ffefe1;
    text-shadow: rgb(75, 74, 74) 2px 2px;

}


form {
  background-color: #b47984;
  width: 75%;
  margin: 40px auto;
  padding: 25px 0px;
  text-align: center;
  border-radius: 10px;
  background-image: linear-gradient(to right, #f19fb0, #b47984, #c25166);
}
input[type="text"], textarea{
  font-size: 1.5rem;
  outline: none;
  background-color: #ffefe1;
  padding: 8px;
  text-align: left;
  border-radius: 3px;
  width: 85%;
  border: 1px solid #fffafa;
  color: black;
  

}
.radio-label {
  color: #fffafa;
  font-size: 2rem;
  font-family: "Marck Script", cursive;
  margin: auto 10px;
  text-shadow: #8a4652 2px 2px;
}
.input-value {
  margin-top: 20px;
}
.input-value {
  margin-top: 20px;
}
.input-content {
  padding-bottom: 170px !important;
}
button {
  font-size: 2rem;
  padding: 0.5% 11%;
  border-radius: 15px;
  color: #8a4652;
  background-color: #fff4e6;
  transition: 0.3s;
  margin-top: 25px;
  border: none;
  outline: none;
}
button:hover{
  background-color: #961a31;
}
::-webkit-input-placeholder {
    color:purple;
  }
:-ms-input-placeholder {
    color:purple;
  }
::-ms-input-placeholder {
    color:purple;
  }
::placeholder {
    color:purple;
  }
  
html{
    margin: 0px;
    font-size: 14px;
}
.nav {
  margin-top: 0px;
  background-color: #b47984;
}
.nav > ul {
  display: flex;
  list-style-type: none;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
}
.nav ul {
  margin-top: 0px;
  text-align: center
}
.nav ul li {
  margin: auto 20px;
}
.nav-link {
  color: #ffefe1;
  text-decoration: none;
  font-weight: lighter;
  font-size: 1.5rem;
  font-family: 'Marck Script', cursive;
}
.add-content{
background-color: #854442;
padding: 6px 13px;
border-radius: 22px;
}
.content{
  display: flex;
  flex-direction: column-reverse;
  grid-gap: 1.4rem;
  gap: 1.4rem;
  width: 80%;
  margin: auto;
  flex-wrap: wrap;
}
.fetch-content h1 , .fetch-content p{
  margin: 0px;
}

.fetch-content{
  background-color: #dadada;
  padding: 20px 35Px ; 
}
@media screen and (max-width:700px){
  
  .nav > ul {
flex-wrap: wrap;
  
  }
  ul{
    padding-left: 0px;
    padding-bottom: 12px;
    font-size: 1rem;
  }
  .add-content{
    display: block;
  }
}
